import Image from 'next/image';
import { useTheme } from 'next-themes';

interface LogoProps {
  size?: number;
  className?: string;
}

export function Logo({ size = 32, className = '' }: LogoProps) {
  const { theme, resolvedTheme } = useTheme();
  const currentTheme = resolvedTheme || theme;

  // Use the appropriate logo based on theme
  const logoSrc = currentTheme === 'dark' ? '/logo-dark.svg' : '/logo-light.svg';

  return (
    <div className={`flex items-center ${className}`}>
      <Image
        src={logoSrc}
        alt="PolyNote Logo"
        width={size}
        height={size}
        className="transition-colors duration-200"
        priority
      />
    </div>
  );
}

// Alternative version using inline SVG for better theme integration
export function LogoSVG({ size = 32, className = '' }: LogoProps) {
  return (
    <div className={`flex items-center ${className}`}>
      <svg 
        width={size} 
        height={size} 
        viewBox="0 0 200 200" 
        fill="none" 
        xmlns="http://www.w3.org/2000/svg"
        className="transition-colors duration-200"
      >
        {/* Outer Rounded Triangle with proper proportions */}
        <path 
          d="M 100 20 L 180 180 L 20 180 Z"
          stroke="currentColor"
          strokeWidth="16"
          strokeLinejoin="round"
          strokeLinecap="round"
          fill="none"
        />

        {/* Inner Abstract Shape - Diagonal line */}
        <path 
          d="M 150 50 L 90 130"
          stroke="currentColor"
          strokeWidth="16"
          strokeLinejoin="round"
          strokeLinecap="round"
        />

        {/* Inner Abstract Shape - Horizontal line */}
        <path 
          d="M 90 130 L 150 130"
          stroke="currentColor"
          strokeWidth="16"
          strokeLinejoin="round"
          strokeLinecap="round"
        />

        {/* Inner Abstract Shape - Parallel line below */}
        <path 
          d="M 90 150 L 150 150"
          stroke="currentColor"
          strokeWidth="16"
          strokeLinejoin="round"
          strokeLinecap="round"
        />
      </svg>
    </div>
  );
}
